import React, { useEffect, useState } from "react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import moment from "moment";
import withDragAndDrop from "react-big-calendar/lib/addons/dragAndDrop";

import "react-big-calendar/lib/addons/dragAndDrop/styles.css";
import "react-big-calendar/lib/css/react-big-calendar.css";
import './MyCalendar.css'
const localizer = momentLocalizer(moment);
const DnDCalendar = withDragAndDrop(Calendar);

const MyCalendar = () => {
  // State for managing events
 
// const [events,setEvents]=useState([]);
 const [tasks, setTasks] = useState([]);


 useEffect(() => {
    fetch("http://localhost:8081/api/dashboard")
      .then((response) => response.json())
      .then((data) => setTasks(data))
      .catch((error) => console.log(error));
  }, []);


  const events = tasks.map((task) => ({
    id: task.id,
    start: moment(task.dueDate).startOf("day").toDate(),
    end: moment(task.dueDate).endOf("day").toDate(),
    title: task.title,
    status: task.status,
  }));
  
  const eventStyleGetter = (event) => {
    let backgroundColor = "#3174ad";
    if (event.status === "COMPLETED") backgroundColor = "green";
    else if (event.status === "OVERDUE") backgroundColor = "red";
    else if (event.status === "IN_PROGRESS") backgroundColor = "blue";

    return {
      style: {
        backgroundColor,
        borderRadius: "5px",
        opacity: 0.9,
        color: "white",
        border: "0px",
        display: "block",
      },
    };
  };

  return (
    <div className="App">
      <DnDCalendar
        defaultDate={moment().toDate()}
        defaultView="month"
        events={events}
        localizer={localizer}
        eventPropGetter={eventStyleGetter}
        resizable
        style={{ height: "100vh" }}
      />
    </div>
  );
};

export default MyCalendar;
