import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';

export default function Taskview() {
    const [tasks,setTasks]=useState([]);
    const { status } = useParams();
    useEffect(()=>{
        fetch(`http://localhost:8081/api/dashboard/${status}`)
        .then(response => response.json())
        .then(data => setTasks(data))
        .catch(error=> console.log(error));

    },[status]);

  
  return (
    <div className="container py-4">
    <h3 className="mb-3 text-capitalize">{status.replace("-", " ")} Tasks</h3>

    {tasks.length === 0 ? (
      <p className="text-muted">No tasks found.</p>
    ) : (
      tasks.map((task) => (
        <div
          className="card mb-3"
          key={task.id}
          style={{
            border: "2px solid",
            borderColor: task.status === "COMPLETED"
            ? "#283618"
            : task.status === "OVERDUE"
            ? "#6a040f"
            : task.status === "IN_PROGRESS"
            ? "#219ebc"
            : "gray",
            borderRadius: "8px",
            
          }}
        >
          <div className="card-body">
          <h4 className="card-title mb-2 text-capitalize text-center">{task.title}</h4>
                    <pre className="card-text mb-2"style={{backgroundColor:"#FFE5D9"}}>DESCRIPTION: {task.description}</pre>
                    <pre className="card-text" style={{backgroundColor:"#FFE5D9"}}>PRIORITY: {task.priority}</pre>
                    <pre className="card-text" style={{backgroundColor:"#FFE5D9"}}>DUE DATE: {task.dueDate.split("T")[0]}</pre>
          </div>
        </div>
      ))
    )}
    </div>
  )
}
