/* Add one feature if completed show in green red for overdue blue for progress and also that there should be line below add task and also box around everytask */
// one more feature to add is that when it is over date it should be shited to overdue and done to completed
//In task container have to add one featue of checkbox of completed and one of delete
// Have feature of sort

import React, { useEffect, useState } from "react";

import { Sidebar, Menu, MenuItem, sidebarClasses } from "react-pro-sidebar";
import { NavLink } from "react-router-dom";
import { PencilIcon } from "@heroicons/react/20/solid";

// Helper function to join class names
function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

export default function Dashboard() {
  const [newTaskName, setNewTaskName] = useState("");
  const [newTaskDescription, setNewTaskDescription] = useState("");
  const [newTaskPriority, setNewTaskPriority] = useState("");
  const [newTaskDueDate, setNewTaskDueDate] = useState("");
  const [tasks, setTasks] = useState([]);
  const [isEditing, setIsEditing] = useState(false);
const [editingTaskId, setEditingTaskId] = useState(null);

  //GET REQUEST
  useEffect(() => {
    fetch("http://localhost:8081/api/dashboard")
      .then((response) => response.json())
      .then((data) => setTasks(data))
      .catch((error) => console.log(error));
  }, []);

  

  const isOverdue = (dueDate) => {
    const today = new Date();
    const dueDateObj = new Date(dueDate);
    return dueDateObj < today;
  };

  
  //POST REQUEST
  // Handle adding a new task
  const handleAddTask = (e) => {
    e.preventDefault();

    const newTask = {
      title: newTaskName,
      description: newTaskDescription,
      priority: newTaskPriority, // keep casing consistent
      dueDate: newTaskDueDate + "T00:00:00", // to ensure correct format
      completed: false,
      status: isOverdue(newTaskDueDate) ? "OVERDUE" : "IN_PROGRESS",
    };

    fetch("http://localhost:8081/api/dashboard", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(newTask),
    })
      .then((response) => response.json())
      .then((data) => {
        setTasks((prev) => [...prev, data]);
        setNewTaskName("");
        setNewTaskDescription("");
        setNewTaskPriority("");
        setNewTaskDueDate("");
        document.getElementById("taskModal").style.display = "none";
      })
      .catch((error) => console.log(error));
  };

  //put request(status)
  const updateTask = (taskId, isCompleted = null) => {
    const task = tasks.find((task) => task.id === taskId);
    if (!task) return;

    const today = new Date();
    const dueDateObj = new Date(task.dueDate);
    const completed = isCompleted !== null ? isCompleted : task.status === "COMPLETED";
    let updatedStatus = task.status;
    if (isCompleted !== null) {
      updatedStatus = isCompleted
        ? "COMPLETED"
        : dueDateObj < today
        ? "OVERDUE"
        : "IN_PROGRESS";
    }
    const updatedTask = { ...task,completed:isCompleted, status: updatedStatus };

    fetch(`http://localhost:8081/api/dashboard/${taskId}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(updatedTask),
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to update task status");
        }
        return response.json();
      })
      .then((data) => {
        setTasks((prevTasks) =>
          prevTasks.map((item) =>
            item.id === taskId ? { ...item, status: data.status } : item
          )
        );
      })
      .catch((error) => {
        console.error("Error updating task status:", error);
      });
  };
   //EDIT FUNCTION
   const resetForm = () => {
    setNewTaskName("");
    setNewTaskDescription("");
    setNewTaskPriority("");
    setNewTaskDueDate("");
    setIsEditing(false);
    setEditingTaskId(null);
    document.getElementById("taskModal").style.display = "none";
  };

  const handleEditClick= (task) => {
    setIsEditing(true);
    setEditingTaskId(task.id);
    setNewTaskName(task.title);
    setNewTaskDescription(task.description);
    setNewTaskPriority(task.priorities);
    setNewTaskDueDate(task.dueDate.split("T")[0]);
    
    // setEditingTaskId(null);
    document.getElementById("taskModal").style.display = "block";
  };

  const handleEditTask = (e) => {
    e.preventDefault();
     
    const updatedTask = {
      title: newTaskName,
      description: newTaskDescription,
      priority: newTaskPriority,
      dueDate: newTaskDueDate + "T00:00:00",
      completed: tasks.find((t) => t.id === editingTaskId)?.status === "COMPLETED",
      status: isOverdue(newTaskDueDate)
        ? "OVERDUE"
        : tasks.find((t) => t.id === editingTaskId)?.completed
        ? "COMPLETED"
        : "IN_PROGRESS",
    };
  
    fetch(`http://localhost:8081/api/dashboard/${editingTaskId}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ ...updatedTask, id: editingTaskId }),
    })
      .then((response) => response.json())
      .then((data) => {
        
        setTasks((prevTasks) =>
          prevTasks.map((task) => (task.id === editingTaskId ? data : task))
        );
        resetForm();
        
      })
      .catch((error) => console.error("Error updating task:", error));
      setIsEditing(false);
  };
  

  //delete request
  const deleteTask = (taskId) => {
    onDeleteTask(taskId);
  };

  const onDeleteTask = (taskId) => {
    fetch(`http://localhost:8081/api/dashboard/${taskId}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to delete task");
        }
        setTasks((prevTasks) =>  prevTasks.filter((task) => task.id !== taskId));
        
      })
       .catch((error) => {
        console.error("FAILED TO DELETE", error);
      });
  };

 

  const sortTasks = (criteria) => {
    const sorted = [...tasks].sort((a, b) => {
      if (criteria === "priority") {
        const priorities = { Low: 1, Medium: 2, High: 3 };
        return priorities[a.priority] - priorities[b.priority];
      } else if (criteria === "dueDate") {
        return new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime();
      }
      return 0;
    });
    setTasks(sorted);
  };

  return (
    <div style={{ display: "flex", minHeight: "100vh" }}>
      {/* Sidebar */}
      <Sidebar
        rootStyles={{
          [`.${sidebarClasses.container}`]: {
            backgroundColor: "rgb(19, 14, 41)",
            color: "#fff",
            minWidth: "250px", // Set a fixed width for the sidebar
          },
        }}
      >
        <div style={{ padding: "20px" }}>
          <h2 className="font-medium">Tasks</h2>
          <Menu
            menuItemStyles={{
              button: {
                [`&.active`]: {
                  backgroundColor: "#13395e",
                  color: "#b6c8d9",
                },
              },
            }}
          >
            <MenuItem>
              <NavLink
                to="/dashboard"
                style={{
                  color: "#fff",
                  display: "block",
                  padding: "10px 15px",
                }}
                activeClassName="active"
              >
                {` All Tasks (${tasks.length})`}
              </NavLink>
            </MenuItem>
            <MenuItem>
              <NavLink
                to="/dashboard/completed"
                style={{
                  color: "#fff",
                  display: "block",
                  padding: "10px 15px",
                }}
                activeClassName="active"
              >
                {`Completed (${
                  tasks.filter((task) => task.status === "COMPLETED").length
                })`}
              </NavLink>
            </MenuItem>
            <MenuItem>
              <NavLink
                to="/dashboard/overdue"
                style={{
                  color: "#fff",
                  display: "block",
                  padding: "10px 15px",
                }}
                activeClassName="active"
              >
                {`Overdue (${
                  tasks.filter((task) => task.status === "OVERDUE").length
                })`}
              </NavLink>
            </MenuItem>

            <MenuItem>
              <NavLink
                to="/dashboard/in_progress"
                style={{
                  color: "#fff",
                  display: "block",
                  padding: "10px 15px",
                }}
                activeClassName="active"
              >
                {` In Progress (${
                  tasks.filter((task) => task.status === "IN_PROGRESS").length
                })`}
              </NavLink>
            </MenuItem>
          </Menu>
        </div>
      </Sidebar>

      {/* Main Content */}
      <div style={{ flexGrow: 1, padding: "20px", backgroundColor:"#FFE5D9"}}>
        <h4>Welcome to the Dashboard!</h4>
        {/* Sort buttons */}
        <div className="mb-4 " >
          <button
            onClick={() => sortTasks("priority")}
            className="btn btn-secondary mr-2"
            style={{border:"1px solid black"}}
          >
            Sort by Priority
          </button>
          <button
            onClick={() => sortTasks("dueDate")}
            className="btn btn-secondary"
            style={{border:"1px solid black"}}
          >
            Sort by Due Date
          </button>
        </div>
        {/* Button to trigger the modal */}
        <div className="flex my-2" style={{borderBottom:"1px solid black"}}>
          <span className=" sm:block">
            <button
              onClick={() =>
                (document.getElementById("taskModal").style.display = "block")
              }
              type="button"
              className="inline-flex items-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 ring-1 shadow-xs ring-gray-300 ring-inset hover:bg-gray-50"
            >
              <PencilIcon
                aria-hidden="true"
                className="mr-1.5 -ml-0.5 size-5 text-gray-400"
              />
              Add Task
            </button>
          </span>
        </div>

        {/* Display tasks */}

        <div className="conatiner" 
                  >
          <div className="row" >
            {tasks.map((task) => (
              <div className="col-md-3 mb-3" key={task.id}>
                <div
                  className="card mb-2"
                  style={{ border: "1px solid #ccc", borderRadius: "5px", width:"20rem" , display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))" }}
                >
                  <div
                    className="card-body" 
                    style={{

                      border:"2px solid",
                      borderColor: task.status === "COMPLETED"
                      ? "#283618"
                      : task.status === "OVERDUE"
                      ? "#6a040f"
                      : task.status === "IN_PROGRESS"
                      ? "#219ebc"
                      : "gray",  
                      
                        }}
                  >
                    <h4 className="card-title mb-2 text-capitalize text-center">{task.title}</h4>
                    <pre className="card-text mb-2" style={{backgroundColor:"#FFE5D9"}}>DESCRIPTION: {task.description}</pre>
                    <pre className="card-text" style={{backgroundColor:"#FFE5D9"}}>PRIORITY: {task.priority}</pre>
                    <pre className="card-text" style={{backgroundColor:"#FFE5D9"}}>DUE DATE: {task.dueDate.split("T")[0]}</pre>
                    <div className="form-check">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        checked={task.status === "COMPLETED"}
                        onChange={(e) => updateTask(task.id, e.target.checked)}
                      />
                      <label className="form-check-label">
                        Mark as Completed
                      </label>
                    </div>
                    <button
                      className="btn btn-danger mt-2"
                      onClick={() => deleteTask(task.id)}
                    >
                      Delete
                    </button>

                    <button
                      className="btn btn-danger mt-2"
                      onClick={()=>handleEditClick(task)}
                      style={{margin:"2px"}}
                    >
                      Edit
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Modal for Adding Task */}
      <div id="taskModal" className="modal" style={{ display: "none" }}>
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">{isEditing ? "Edit Task" : "Add New Task"}</h5>
              <button
                type="button"
                className="close"
                onClick={resetForm}
              >
                <span>&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <form onSubmit={isEditing ? handleEditTask :handleAddTask}>
                <div className="mb-3">
                  <label className="form-label">Task Name</label>
                  <input
                    type="text"
                    className="form-control"
                    value={newTaskName}
                    onChange={(e) => setNewTaskName(e.target.value)}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Description</label>
                  <textarea
                    className="form-control"
                    value={newTaskDescription}
                    onChange={(e) => setNewTaskDescription(e.target.value)}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Priority</label>
                  <select
                    className="form-select"
                    value={newTaskPriority}
                    onChange={(e) => setNewTaskPriority(e.target.value)}
                    required
                  >
                    <option value="Low">Low</option>
                    <option value="Medium">Medium</option>
                    <option value="High">High</option>
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label">Due Date</label>
                  <input
                    type="date"
                    className="form-control"
                    value={newTaskDueDate}
                    onChange={(e) => setNewTaskDueDate(e.target.value)}
                    required
                  />
                </div>
                <button type="submit " className="btn btn-primary">
                {isEditing ? "Edit Task" : "Add New Task"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
